@use "base/variables" as v;
@use "base/mixins" as m;
.contenedor-testimonios-esp {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}
.contenedor-testimonios {
    h1 {
        color: v.$negro;
        margin: 2rem 0;
    }
    .testimonios {
        display: none;
        &.active {
            display: flex;
        }
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 2rem;
        margin: 0 1rem;
        @include m.desktopXL {
            flex-direction: row;
        }

        animation: fadeIn 0.5s ease-in-out;
    }
    .botones-testimonios {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 2rem;
        margin: 1rem;
        button.boton {
            width: 3.5rem;
            background-color: rgb(0 0 0 / 0.5);
            color: v.$blanco;
            border: none;
            cursor: pointer;
            padding: 1rem;
            border-radius: 2rem;
            transition: all 150ms ease-in;

            &:hover {
                background-color: v.$blanco;
                color: darken(v.$blanco, $amount: 50);
            }
        }
    }
}
.testimonio {
    @include m.grid-2-esp(1rem);
    flex: 0 0 calc(50% - 2rem);
    max-width: 80rem;
    border: 1px solid v.$rojo;
    border-radius: 2rem;
    img.testimonio-img {
        max-width: 20rem;
        align-self: center;
        justify-self: center;
    }
}

.testimonio-info {
    p {
        text-align: justify;
        margin: 0 1rem;
        @include m.desktopXL {
            margin: 0;
            margin-right: 1rem;
        }
    }
    h2 {
        margin-left: 1rem;
        margin-right: 2rem;
        @include m.desktopXL {
            margin-left: 0;
        }
    }
}
